*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: var(--font1), "Noto Sans CJK KR Regular";
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  font-size: 10px;
}

article, aside, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}

/*
  수정 날짜 : 2020.06.02
  작성자 : 이주연
  내용 : 아차 2차 디자인 반영 body / color, font-family 변경
*/
body {
  margin: 0;
  font-family: var(--font1), "Noto Sans CJK KR Regular", dotum, 돋움, "굴림", Gulim, Tahoma, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: var(--color-text-default);
  text-align: left;
  background-color: #fff;
}

html, body {
  width: 100%;
  height: 100%;
}

iframe {
  width: 100%;
  height: 100%;
  background: #ffffff;
  border: none;
}

[tabindex="-1"]:focus {
  outline: 0 !important;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-weight: normal;
}

p {
  margin: 0;
}

abbr[title],
abbr[data-original-title] {
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
  cursor: help;
  border-bottom: 0;
  text-decoration-skip-ink: none;
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}

ol,
ul,
dl {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: 700;
}

dd {
  margin-bottom: .5rem;
  margin-left: 0;
}

blockquote {
  margin: 0 0 1rem;
}

b,
strong {
  font-weight: normal;
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

a {
  color: var(--color-text-default);
  text-decoration: none;
  background-color: transparent;
  cursor: pointer;
}

a:hover {
  color: var(--color-text-default);
  text-decoration: none;
}


a:not([href]):not([tabindex]):focus {
  outline: 0;
}

pre,
code,
kbd,
samp {
  font-family: var(--font1), "Noto Sans CJK KR Regular", dotum, 돋움, "굴림", Gulim, Tahoma, sans-serif;
  font-size: 1em;
}

i, cite, em, var, address, dfn {
  font-style: normal;
}

pre {
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto;
}

figure {
  margin: 0 0 1rem;
}

img {
  vertical-align: middle;
  border-style: none;
}

svg {
  overflow: hidden;
  vertical-align: middle;
}

table {
  border-collapse: collapse;
}

caption {
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  width: 1px;
  height: 1px;
  font-size: 0;
  line-height: 0;
}

th {
  text-align: inherit;
}

label {
  display: inline-block;
  cursor: pointer;
  font-weight: bold;
  font-size: 16px;
}

button {
  border-radius: 0;
  border: 0;
}

button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  padding: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: normal;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type="radio"],
input[type="checkbox"] {
  box-sizing: border-box;
  padding: 0;
}

input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  -webkit-appearance: listbox;
}

textarea {
  overflow: auto;
  resize: vertical;
  background-color: #fff;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  overflow: hidden;
  position: absolute;
  left: -999999px;
  clip: rect(0, 0, 0, 0); /*ie6,7*/
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
}

progress {
  vertical-align: baseline;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  outline-offset: -2px;
  -webkit-appearance: none;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

output {
  display: inline-block;
}

summary {
  display: list-item;
  cursor: pointer;
}

template {
  display: none;
}

[hidden] {
  display: none !important;
}

.clearfix::after {
  display: block;
  clear: both;
  content: "";
}

.medium {
  font-family: var(--font2);
  font-weight: 500;
}

.light {
  font-weight: 300;
}

.bold {
  font-weight: bold
}

.normal {
  font-family: var(--font1);
}

.underline {
  text-decoration: underline;
}

.align-left {
  text-align: left;
}

.align-right {
  text-align: right;
}

.align-center {
  text-align: center;
}
